<template>
		<div class="fighter">
			<span><strong>{{ params.name }}</strong></span><br>
			<span>Ловкость: {{ params.agility }} &nbsp;&nbsp;&nbsp;</span>
			<transition name="slide-fade" mode="out-in">
				<span :key=params.strength>Сила: {{ params.strength }}</span>
			</transition>
			<br>
			<div v-if="params.name !== 'Герой'">
				<button class="myBtn" :disabled=!heroAlive @click="$emit('fight')">В бой</button>
				<button class="myBtn" :disabled=!heroAlive @click="$emit('deleteFighter')">Удалить</button>
			</div>
		</div>
</template>

<script>
export default {
	props: {
		params: Object,
		heroAlive: Boolean
	}
}
</script>

<style scoped>
.fighter {
	font-size: 18px;
}
</style>