<template>
	<div class="dice-fight row">

		<h3>Симуляция боев для книги-игры</h3>

		<div class="battlefield">
			<transition name="flip" mode="out-in">
				<div class="col-6" v-if="fighters[0].isAlive" key="hero">
					<span class="paramsText">Параметры героя</span>
					<input class="newValues" v-model.number="fighters[0].agility" />
					<input class="newValues" v-model.number="fighters[0].strength" />
					<fighter :params="fighters[0]"></fighter>
					<transition name="slide-fade" mode="out-in">
						<span class="msg" v-if="isMessageActive" :key=message><strong>{{ message }}</strong></span>
					</transition>
				</div>
				<div class="col-6" v-else key="msg">
					<span><strong>ГЕРОЙ ПОГИБ!</strong></span><br>
					<button class="myBtn start" @click="start">Начать новый бой</button>
				</div>
			</transition>
			
			<div class="col-6">
				<input class="newValues" :disabled=!fighters[0].isAlive v-model.number="newFighter.agility"/>
				<input class="newValues" :disabled=!fighters[0].isAlive v-model.number="newFighter.strength"/>
				<button class="myBtn newValBtn" :disabled=!fighters[0].isAlive @click="addFighter(newFighter.agility, newFighter.strength)">Добавить врага</button>
				<transition-group name="fade" mode="out-in">
					<ul class="enemies" v-for="(enemy, i) in fighters" :key=enemy.name>
						<fighter v-if="i > 0" :params="fighters[i]" :heroAlive=fighters[0].isAlive @fight="fight(i)" @deleteFighter="deleteFighter(i)"></fighter>
					</ul>
				</transition-group>
			</div>
		</div>

	</div>
</template>

<script>
import fighter from './components/fighter.vue';

export default {
	name: 'app',
	components: {
		'fighter': fighter
	},
	data () {
		return {
			isMessageActive: 0,
			message: 'Противник был ранен!',
			newFighter: {
				agility: 0,
				strength: 0,
			},
			fighters: [
			{
				name: 'Герой',
				agility: 10,
				strength: 10,
				isAlive: 1
			},
			{
				name: 'Враг 1',
				agility: 10,
				strength: 10
			}
			]
		}
	},
	computed: {
		myPower(){
			return mtRand(1,6)*2 + this.fighters[0].agility;
		}
	},
	methods: {
		start() {
			let obj = {
				name: 'Герой',
				agility: 10,
				strength: 20,
				isAlive: 1
			}
			this.$set(this.fighters, 0, obj);
			this.isMessageActive = 0;
		},
		fight(position){
			this.isMessageActive = 1;
			for (var i = 1; i < this.fighters.length; i++) {
				let enemyPower = mtRand(1,6)*2 + this.fighters[i].agility;
				if (this.myPower > enemyPower) {
					if (i == position) {
						if (this.fighters[i].strength <= 2) {
							this.deleteFighter(i)
							this.message = 'Враг убит!';
						} else {
							let obj = {
								name: this.fighters[i].name,
								agility: this.fighters[i].agility,
								strength: this.fighters[i].strength -2,
								isAlive: 1
							}
							this.$set(this.fighters, i, obj);
							this.message = 'Враг ранен!';
						}
					}
				} else if (this.myPower < enemyPower) {
					this.message = 'Герой ранен!';
					let obj = {
						name: 'Герой',
						agility: this.fighters[0].agility,
						strength: this.fighters[0].strength - 2,
						isAlive: this.fighters[0].strength <= 2 ? 0 : 1
					};
					this.$set(this.fighters, 0, obj);
				} else {
					this.message = 'Парирование удара!';
				}
			}
		},
		addFighter(agi, str){
			let newFighter = {
				name: `Враг ${this.fighters.length}`,
				agility: agi,
				strength: str
			}
			this.fighters.push(newFighter);
			this.message = 'Новый враг!';
			this.isMessageActive = 1;
		},
		deleteFighter(position){
			this.fighters.splice(position, 1);
		}
	}
}

function mtRand(min, max){
	let diff = max - min;
	return Math.floor(Math.random() * (diff + 1)) + min;
}
</script>

